
.home-page {
  min-height: 100vh;
  width: 100%;
  position: relative;
  overflow: hidden;
}

body, html {
  margin: 0;
  padding: 0;
  height: 100%;
}

.particles-background {
  position: absolute !important;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 0;
}

.background-overlay {
  position: relative;
  width: 100%;
  min-height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1;
}
p {
  color: white;
  margin-bottom: 1rem;

}

.content-container {
  max-width: 800px;
  width: 90%;
  padding: 2rem;
  text-align: center;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.profile-image-placeholder {
  width: 150px;
  height: 150px;
  border-radius: 50%;
  border: 3px solid white;
  margin-bottom: 2rem;
  display: flex;
  align-items: center;
  justify-content: center;
  overflow: hidden;
}

.profile-image-placeholder img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

h1, h2 {
  color: white;
  font-size: 2rem;
}

p {
  font-size: 1.2rem;
}

.about-section, .ai-section {
  margin-bottom: 2rem;
  max-width: 600px;
  width: 100%;
}

.ai-section p {
  text-align: left;
  line-height: 1.6;
  margin-bottom: 1.2rem;
}

.navigation-buttons-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 20px;
}

.navigation-buttons {
  display: flex;
  gap: 20px;
  margin-bottom: 1rem;
}

.demos-button, .cv-button {
  padding: 12px 30px;
  background-color: #60a5fa;
  color: white;
  border: 2px solid #60a5fa;
  border-radius: 8px;
  cursor: pointer;
  font-size: 1.1rem;
  font-weight: 600;
  transition: all 0.3s ease;
  box-shadow: 0 4px 6px rgba(96, 165, 250, 0.3);
}

.demos-button:hover, .cv-button:hover {
  background-color: transparent;
  border-color: #60a5fa;
  color: #60a5fa;
  transform: translateY(-2px);
  box-shadow: 0 6px 12px rgba(96, 165, 250, 0.5);
}

.programming-skills {
  display: flex;
  gap: 20px;
  justify-content: center;
  margin-bottom: 2rem;
}

.programming-skills svg {
  color: white;
  opacity: 0.8;
  transition: opacity 0.3s ease;
}

.programming-skills svg:hover {
  opacity: 1;
}

.contact-links-section {
  margin-top: 2rem;
  padding-top: 1rem;
  border-top: 1px solid rgba(255, 255, 255, 0.2);
}

.contact-links-section p {
  color: white;
  margin-bottom: 1rem;
  font-size: 1rem;
}

@media (max-width: 768px) {
  .content-container {
    width: 95%; /* Wider on mobile */
    padding: 1rem; /* Reduce padding */
  }

  .profile-image-placeholder {
    width: 120px;
    height: 120px;
    margin-bottom: 1.5rem;
  }

  h1 {
    font-size: 1.8rem; /* Smaller font size */
  }

  h2 {
    font-size: 1.8rem;
  }


}

@media (max-width: 768px) {
  .navigation-buttons {
    flex-direction: column; /* Stack buttons vertically */
    gap: 15px;
  }

  .demos-button, .cv-button {
    width: 100%; /* Full-width buttons */
    padding: 12px 15px; /* Larger touch target */
  }

  .programming-skills {
    flex-wrap: wrap; /* Allow wrapping */
    justify-content: center;
    gap: 15px;
  }

  .programming-skills svg {
    size: 40; /* Slightly smaller icons */
  }
}